h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
ul,
span,
li,
img,
inpot,
button {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.5;
  font-weight: inherit;
}

h1,
h2,
h3 {
  font-family: "Poppins", cursive;
}

p {
  line-height: 1.6;
  font-size: 1.05em;
  font-weight: 400;
  color: #777;
}

h1 {
  font-size: 4em;
  line-height: 1;
}

h2 {
  font-size: 2.2em;
  line-height: 1.1;
}

h3 {
  font-size: 1.8em;
}

h4 {
  font-size: 1.3em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.9em;
  letter-spacing: 1px;
}

a,
button {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  line-height: 1;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

a:focus,
a:active,
a:hover,
button:focus,
button:active,
button:hover,
a b.light-color:hover {
  text-decoration: none;
  color: #ffa804;
}

b {
  font-weight: 600;
}

img {
  width: 100%;
}

li {
  list-style: none;
  display: inline-block;
}

span {
  display: inline-block;
}

button {
  outline: 0;
  border: 0;
  background: none;
  cursor: pointer;
}

.icon {
  font-size: 1.1em;
  display: inline-block;
  line-height: inherit;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  line-height: inherit;
}

b.max-bold {
  font-weight: 700;
}

.section {
  padding: 100px 0 50px;
}

.section .heading {
  margin-bottom: 60px;
}

.section .heading:before {
  content: "";
  height: 10px;
  width: 50px;
  border-radius: 5px;
  margin-bottom: 20px;
  display: block;
  background: #ffa804;
}

.margin-t-10 {
  margin-top: 10px;
}

.margin-b-30 {
  margin-bottom: 30px;
}

.margin-b-50 {
  margin-bottom: 50px;
}

.margin-tb-30 {
  margin-top: 30px;
  margin-bottom: 30px;
}

.font-yellow {
  color: #ffa600;
}

.font-semi-white {
  color: #ddd;
}

.font-lite-black {
  color: #999;
}

header {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  z-index: 100;
}

header .heading-wrapper {
  border-radius: 5px;
  padding: 30px 40px 20px;
  background: #ffa600;
  color: #fff;
}

header .info {
  margin-bottom: 10px;
}

header .info .icon {
  float: left;
  font-size: 30px;
}

header .info .right-area {
  margin-left: 45px;
}

header .downlad-btn {
  padding: 15px 30px;
  border-radius: 4px;
  float: right;
  margin-top: 30px;
  box-shadow: inset 0 0px 4px rgba(0, 0, 0, 0.3);
  background: #ffa600;
  color: #fff;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

header .downlad-btn:hover {
  -webkit-transform: translateY(-4px);
  transform: translateY(-4px);
}

.intro-section {
  padding-top: 200px;
  position: relative;
  background-size: cover;
  z-index: 1;
  background-image: url(/background.jfif);
}

.intro-section:before {
  content: "";
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
}

.intro-section:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 150px;
  z-index: -1;
  background: #fff;
}

.intro {
  padding: 0 20px;
  margin-top: 100px;
  text-align: center;
  background: #fff;
}

.intro .profile-img {
  height: 200px;
  width: 200px;
  border-radius: 100px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  top: -100px;
  margin-bottom: -70px;
}

.intro .information > li {
  display: block;
  margin: 5px 0;
}

.intro .social-icons > li > a {
  height: 35px;
  line-height: 35px;
  width: 35px;
  border-radius: 20px;
  overflow: hidden;
  z-index: 1;
  position: relative;
  margin: 0 5px 5px;
  background: #887392;
  color: #fff;
}

.intro .social-icons > li > a:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  z-index: -1;
  border-radius: 20px;
  background: #ffa804;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.intro .social-icons > li > a:hover:after {
  -webkit-transform: scale(1);
  transform: scale(1);
}

.portfolio-section {
  padding-bottom: 0;
}

.portfolio-section .portfolioFilter {
  margin-top: 60px;
  color: #666;
}

.portfolio-section .portfolioFilter a {
  margin: 10px 10px 0 0;
}

.portfolio-section .portfolioFilter a.current {
  color: #ffa804;
}

.portfolio-section .portfolioContainer {
  margin-left: -15px;
  margin-right: -15px;
}

.portfolio-section .p-item {
  width: 33.333%;
  padding: 0 7.5px 0px;
  margin-top: 15px;
}

.portfolio-section .p-item a {
  display: inline;
}

.portfolio-section .p-item.p-item-2 .img:first-child {
  float: left;
  width: 50%;
  padding-right: 7.5px;
}

.portfolio-section .p-item.p-item-2 .img:last-child {
  float: right;
  width: 50%;
  padding-left: 7.5px;
}

.experience-section {
  background: #28023d;
  color: #fff;
}

.experience .list > li {
  display: block;
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
}

.experience .list > li:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 10px;
  width: 10px;
  margin-top: -5px;
  display: block;
  border-radius: 10px;
  border: 2px solid #fff;
}

.education-wrapper {
  position: relative;
}

.education-wrapper:after {
  content: "";
  position: absolute;
  top: 8px;
  bottom: 0;
  left: -51px;
  width: 2px;
  background: #ccc;
}

.education-wrapper .education {
  position: relative;
}

.education-wrapper .education:after {
  content: "";
  position: absolute;
  top: 8px;
  left: -58px;
  width: 16px;
  height: 16px;
  border-radius: 10px;
  z-index: 1;
  box-shadow: 0 0 0 8px rgba(255, 171, 0, 0.4);
  background: #ffab00;
}

.education-wrapper .list > li {
  display: block;
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
  color: #777;
}

.education-wrapper .list > li:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 10px;
  width: 10px;
  margin-top: -5px;
  display: block;
  border-radius: 10px;
  border: 2px solid #777;
}

