/* RADIAL PREOGRESS SECTION */

.radial-progress {
  position: relative;
  max-width: 200px;
  margin: 0 auto;
}

.radial-progress .progressbar-text {
  font-size: 2.2em;
  font-weight: 500;
  padding-bottom: 25px !important;
  color: #333 !important;
}

.radial-progress .progress-title {
  position: absolute;
  top: 45%;
  left: 50%;
  width: 100%;
  letter-spacing: 0;
  text-align: center;
  transform: translate(-50%, 15px);
  color: #777;
}

.radial-progress .progress-percentage {
  position: absolute;
  top: 30%;
  width: 100%;
  letter-spacing: 0;
  text-align: center;
  color: #000;
}

.copyright {
  text-align: center;
  padding: 30px 0;
  background: #28023d;
}

/* Screens Resolution : 992px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 1200px) {
  .counter-section {
    text-align: center;
    padding: 50px 0 20px;
  }

  .counter {
    display: inline-block;
  }

  .counter .desc {
    text-align: left;
  }

  h6 {
    font-size: 0.8em;
    letter-spacing: 1px;
  }
}

/* Screens Resolution : 992px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 992px) {
  .intro-section {
    padding-top: 250px;
  }
}

/* Screens Resolution : 767px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 767px) {
  p {
    line-height: 1.4;
  }

  h1 {
    font-size: 3em;
  }

  h2 {
    font-size: 2em;
  }

  h3 {
    font-size: 1.8em;
  }

  .section {
    padding: 50px 0 0px;
  }

  .section .heading {
    margin-bottom: 30px;
  }

  .intro-section {
    padding-top: 350px;
  }

  .portfolio-section {
    padding-bottom: 0;
  }

  .portfolio-section .portfolioFilter {
    margin: 0px 0 10px;
  }

  .portfolio-section .portfolioContainer {
    margin-left: 0;
    margin-right: 0;
  }

  .portfolio-section .p-item {
    width: 50%;
    padding: 0 15px 0px;
  }

  .portfolio-section .p-item {
    max-width: 400px;
  }

  .education-wrapper:after,
  .education-wrapper .education:after {
    display: none;
  }
}

/* Screens Resolution : 575px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 575px) {
  body {
    font-size: 14px;
  }

  .portfolio-section .p-item {
    width: 100%;
  }
}

/* Screens Resolution : 479px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 479px) {
}

/* Screens Resolution : 359px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 359px) {
  header .heading-wrapper {
    padding: 30px 20px 20px;
  }

  header .info .icon {
    font-size: 25px;
  }

  header .info .right-area {
    margin-left: 35px;
  }
}

/* Screens Resolution : 290px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 290px) {
}

/* Screens Resolution : 290px
-------------------------------------------------------------------------- */
@media only screen and (max-width: 260px) {
  header .heading-wrapper {
    padding: 10px 15px 0px;
  }

  header .info .icon {
    font-size: 15px;
  }

  header .info .right-area {
    margin-left: 20px;
  }

  .intro .profile-img {
    width: 80%;
    height: auto;
    top: 0;
    margin-bottom: 10px;
  }

  .intro {
    padding: 20px 10px 0;
    margin-right: -15px;
    margin-left: -15px;
  }
}
